<template>
    <div>
        <ul class="cart__orders">
            <li class="cart__order" v-for='product in products' :key='product.id'>
                <h3>{{ product.product.title }}</h3>
                <b>{{ product.price }} ₽</b>
                <b>{{ product.quantity }} шт.</b>
                <span>Артикул: {{ product.id }}</span>
            </li>
        </ul>
        <div class="cart__total">
            <p>Доставка: <b>500 ₽</b></p>
            <p>Итого: <b>{{ products.length }}</b> товара на сумму 
                <b>{{ totalPrice |numberFormat}} ₽</b></p>
        </div>

    </div>
</template>

<script>
import numberFormat from '@/helpers/numberFormat';

export default {
    props: {
        products: {
            default: [],
        }
    },
    computed: {
        totalPrice() {
            return Number(this.products.reduce((acc, item) => 
            (item.price * item.quantity) + acc, 0));
        },
        
    },
    filters: {
    numberFormat,
  },
}
</script>